@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: 'Inter', sans-serif;
  background: linear-gradient(135deg, #030014 0%, #0a0322 25%, #1a0b47 50%, #0f0729 75%, #030014 100%);
  background-size: 400% 400%;
  animation: gradientShift 12s ease infinite;
  min-height: 100vh;
  overflow-x: hidden;
}

/* Ensure 3D background doesn't interfere with interactions */
canvas {
  pointer-events: none !important;
}

@keyframes gradientShift {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

/* Flowing Light Veins Background */
.vein-background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: -1;
  overflow: hidden;
}

.vein-network {
  position: absolute;
  width: 100%;
  height: 100%;
}

.vein-path {
  position: absolute;
  background: linear-gradient(90deg, transparent, rgba(145, 94, 255, 0.6), rgba(78, 205, 196, 0.4), rgba(255, 107, 107, 0.3), transparent);
  border-radius: 2px;
  box-shadow: 0 0 8px rgba(145, 94, 255, 0.3);
}

/* Branching veins for more complex network */
.vein-branch {
  position: absolute;
  background: linear-gradient(45deg, transparent, rgba(145, 94, 255, 0.3), transparent);
  border-radius: 1px;
}

.vein-branch-1 {
  width: 80%;
  height: 1px;
  top: 35%;
  left: 10%;
  transform: rotate(45deg);
  animation: flowBranch 6s linear infinite;
}

.vein-branch-2 {
  width: 60%;
  height: 1px;
  top: 65%;
  right: 15%;
  transform: rotate(-35deg);
  animation: flowBranch 8s linear infinite;
  animation-delay: -3s;
}

.vein-branch-3 {
  width: 70%;
  height: 1px;
  top: 50%;
  left: 25%;
  transform: rotate(65deg);
  animation: flowBranch 7s linear infinite;
  animation-delay: -1.5s;
}

.vein-path-1 {
  width: 200%;
  height: 2px;
  top: 20%;
  left: -50%;
  transform: rotate(15deg);
  animation: flowRight 8s linear infinite;
}

.vein-path-2 {
  width: 150%;
  height: 3px;
  top: 45%;
  left: -25%;
  transform: rotate(-10deg);
  animation: flowRight 12s linear infinite;
  animation-delay: -2s;
}

.vein-path-3 {
  width: 180%;
  height: 2px;
  top: 70%;
  left: -40%;
  transform: rotate(25deg);
  animation: flowRight 10s linear infinite;
  animation-delay: -4s;
}

.vein-path-4 {
  width: 120%;
  height: 2px;
  top: 30%;
  right: -20%;
  transform: rotate(-20deg);
  animation: flowLeft 9s linear infinite;
  animation-delay: -1s;
}

.vein-path-5 {
  width: 160%;
  height: 3px;
  top: 60%;
  right: -30%;
  transform: rotate(12deg);
  animation: flowLeft 11s linear infinite;
  animation-delay: -3s;
}

.vein-path-6 {
  width: 140%;
  height: 2px;
  top: 85%;
  right: -20%;
  transform: rotate(-30deg);
  animation: flowLeft 7s linear infinite;
  animation-delay: -5s;
}

/* Vertical flowing veins */
.vein-path-vertical-1 {
  width: 2px;
  height: 200%;
  left: 15%;
  top: -50%;
  transform: rotate(5deg);
  animation: flowDown 15s linear infinite;
}

.vein-path-vertical-2 {
  width: 3px;
  height: 180%;
  left: 60%;
  top: -40%;
  transform: rotate(-8deg);
  animation: flowDown 18s linear infinite;
  animation-delay: -6s;
}

.vein-path-vertical-3 {
  width: 2px;
  height: 160%;
  left: 85%;
  top: -30%;
  transform: rotate(12deg);
  animation: flowUp 14s linear infinite;
  animation-delay: -2s;
}

/* Pulsing energy orbs */
.energy-orb {
  position: absolute;
  border-radius: 50%;
  background: radial-gradient(circle, rgba(145, 94, 255, 0.8), rgba(145, 94, 255, 0.2), transparent);
  animation: pulse 3s ease-in-out infinite;
}

.energy-orb-1 {
  width: 8px;
  height: 8px;
  top: 25%;
  left: 20%;
  animation-delay: 0s;
}

.energy-orb-2 {
  width: 12px;
  height: 12px;
  top: 55%;
  left: 70%;
  animation-delay: 1s;
}

.energy-orb-3 {
  width: 6px;
  height: 6px;
  top: 75%;
  left: 35%;
  animation-delay: 2s;
}

.energy-orb-4 {
  width: 10px;
  height: 10px;
  top: 40%;
  left: 80%;
  animation-delay: 0.5s;
}

/* Flowing light particles */
.light-particle {
  position: absolute;
  width: 4px;
  height: 4px;
  background: rgba(255, 255, 255, 0.9);
  border-radius: 50%;
  box-shadow: 0 0 10px rgba(145, 94, 255, 0.8);
}

.light-particle-1 {
  top: 20%;
  left: 0%;
  animation: particleFlow1 6s linear infinite;
}

.light-particle-2 {
  top: 45%;
  left: 0%;
  animation: particleFlow2 8s linear infinite;
  animation-delay: -2s;
}

.light-particle-3 {
  top: 70%;
  left: 0%;
  animation: particleFlow3 7s linear infinite;
  animation-delay: -4s;
}

@keyframes flowRight {
  0% { transform: translateX(-100%) rotate(15deg); opacity: 0; }
  10% { opacity: 1; }
  90% { opacity: 1; }
  100% { transform: translateX(100%) rotate(15deg); opacity: 0; }
}

@keyframes flowLeft {
  0% { transform: translateX(100%) rotate(-20deg); opacity: 0; }
  10% { opacity: 1; }
  90% { opacity: 1; }
  100% { transform: translateX(-100%) rotate(-20deg); opacity: 0; }
}

@keyframes flowDown {
  0% { transform: translateY(-100%) rotate(5deg); opacity: 0; }
  10% { opacity: 1; }
  90% { opacity: 1; }
  100% { transform: translateY(100%) rotate(5deg); opacity: 0; }
}

@keyframes flowUp {
  0% { transform: translateY(100%) rotate(12deg); opacity: 0; }
  10% { opacity: 1; }
  90% { opacity: 1; }
  100% { transform: translateY(-100%) rotate(12deg); opacity: 0; }
}

@keyframes pulse {
  0%, 100% { opacity: 0.4; transform: scale(1); }
  50% { opacity: 1; transform: scale(1.2); }
}

@keyframes particleFlow1 {
  0% { transform: translate(0, 0); opacity: 0; }
  5% { opacity: 1; }
  25% { transform: translate(25vw, -10vh); }
  50% { transform: translate(50vw, 5vh); }
  75% { transform: translate(75vw, -5vh); }
  95% { opacity: 1; }
  100% { transform: translate(100vw, 10vh); opacity: 0; }
}

@keyframes particleFlow2 {
  0% { transform: translate(0, 0); opacity: 0; }
  5% { opacity: 1; }
  25% { transform: translate(30vw, 8vh); }
  50% { transform: translate(60vw, -8vh); }
  75% { transform: translate(85vw, 3vh); }
  95% { opacity: 1; }
  100% { transform: translate(100vw, -5vh); opacity: 0; }
}

@keyframes particleFlow3 {
  0% { transform: translate(0, 0); opacity: 0; }
  5% { opacity: 1; }
  25% { transform: translate(20vw, 5vh); }
  50% { transform: translate(45vw, -12vh); }
  75% { transform: translate(70vw, 8vh); }
  95% { opacity: 1; }
  100% { transform: translate(100vw, -3vh); opacity: 0; }
}

/* Enhanced text visibility */
.text-primary-enhanced {
  color: #ffffff;
  text-shadow: 0 2px 8px rgba(0, 0, 0, 0.8);
}

.text-secondary-enhanced {
  color: #dfd9ff;
  text-shadow: 0 1px 4px rgba(0, 0, 0, 0.6);
}

/* Glowing effects */
.glow-effect {
  box-shadow: 0 0 20px rgba(145, 94, 255, 0.4);
}



/* Gradient text effect */
.gradient-text {
  background: linear-gradient(45deg, #915EFF, #4ECDC4, #FF6B6B, #FFA726);
  background-size: 300% 300%;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: gradientTextShift 4s ease infinite;
}

@keyframes gradientTextShift {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

/* Pulsing elements */
.pulse-glow {
  animation: pulseGlow 2s ease-in-out infinite alternate;
}

@keyframes pulseGlow {
  from { box-shadow: 0 0 10px rgba(145, 94, 255, 0.4); }
  to { box-shadow: 0 0 30px rgba(145, 94, 255, 0.8); }
}

/* Background Stars */
.star-field {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: -2;
}

.star {
  position: absolute;
  background: rgba(255, 255, 255, 0.15);
  border-radius: 50%;
  animation: twinkle 4s ease-in-out infinite alternate;
}

.star-small {
  width: 1px;
  height: 1px;
}

.star-medium {
  width: 2px;
  height: 2px;
}

.star-large {
  width: 3px;
  height: 3px;
}

@keyframes twinkle {
  0% { opacity: 0.3; }
  100% { opacity: 0.8; }
}

